*{
    margin: 0;
    padding: 0;
}
html{

    font-family: Calibri, Helvetica, sans-serif;  
}
      
  form{
    color: red;
  }
  .container {  
    padding: 50px;  
    background-color: white;  
  }  
    
  input[type=text], input[type=password], textarea {  
    width: 100%;  
    padding: 15px;  
    margin: 5px 0 22px 0;  
    display: inline-block;  
    border: none;  
    /* background: #f1f1f1;   */
    background: rgba(128, 128, 128, 0.274);
    border-radius: 20px;
  }  
  input[type=text]:focus, input[type=password]:focus {  
    background-color: rgba(255, 0, 0, 0.04);  
    outline: none;  
  }  
  /* div {  
    padding: 10px 0;  
   }   */
  hr {  
    border: 1px solid #f1f1f1;  
    margin-bottom: 25px;  
  }  
  .registerbtn {  
    background-color: red;  
    color: white;  
    padding: 16px 20px;  
    margin: 8px 0;  
    border: none;  
    cursor: pointer;  
    opacity: 0.9;  
    border-radius: 20px;
  }  
  .registerbtn:hover {  
    opacity: 1;  
  }  
  a.rbfr{
    text-decoration: none;
    color: red;
  }
  .down-btns{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .triangle{
    width: 50px;
    height: 50px;
    background: red;
    position: fixed;
  }
  .triangle--up{
    clip-path: polygon(0 0, 0 100%, 100% 0);
    top: 0;
    left: 0;
  }
  .triangle--down{
    clip-path: polygon(100% 100%, 0 100%, 100% 0);
    bottom: 0;
    right: 0;
  }


  /* profile screen */
  .nav{
    background: red;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .nav .left-side{
    display: flex;
    align-items: center;
    gap: 20px;
  }
  .fa{
    color: white;
  }
  .logo_box{
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid red;
    box-sizing: border-box;
    width: 20vw;
    padding: 10px 0;
    cursor: pointer;
  }
  .logo_box--hide{
    display: none;
  }
  .logo{
    height: 30px;
  }
  .profile{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: whitesmoke;
    cursor: pointer;
  }
  .nav_links{
    display: flex;
    gap: 20px;
    align-items: center;
    padding-right: 20px;
  }
  a.nav_links_anchor{
    text-decoration: none;
    color: white;
    font-weight: bold;
    position: relative;
  }
  a.nav_links_anchor:hover{
    background: rgba(255, 255, 255, 0.136);
    color: black;
    transition: color, 2s ease-in-out;
  }
  a.nav_links_anchor:hover > .lines{
    display: block;
  }
  .lines{
    width: 100%;
    height: 2px;
    position: absolute;
    background: white;
    bottom: -7px;
    animation: line 2s ease-in-out;
    display: none;
  }
  @keyframes line {
    from{
      width: 10px;
    }
    to{
      width: 100%;
    }
  }
  .toggler{
    cursor: pointer;
  }
  .main{
    width: 100%;
    display: flex;
  }
  .main_sidebar{
    height: calc(100vh - 50px);
    background: white;
    display: none;
  }
  .main_sidebar--show{
    display: block;
    width: 25vw;
    overflow-y: auto;
    border-right: 1px dotted red;
    box-shadow: 10px 10px 10px rgba(255, 0, 0, 0.216);
    box-sizing:  border-box;
  }
  .main_sidebar--show ul{
    list-style: none;
  }
  .main_sidebar--show ul li{
    display: flex;
    justify-content: center;
    padding: 10px;
    justify-content: space-between;
    border-bottom: 1px solid rgba(128, 128, 128, 0.274);
  }
  .main_sidebar--show ul li a{
    text-decoration: none;
    color: red;
  }
  .main_body{
    width: 100%;
    height: calc(100vh - 50px);
    padding: 15px;
    overflow-y: auto;
    box-sizing: border-box;
    background: whitesmoke;
  }
  
  .wraper{
    position: fixed;
    width: 100%;
    height: 100%;
  }